@use '../abstracts/variables';

section.page404{
  width: 100vw;
  height: 100vh;
  position: relative;

  h1 {
    @include variables.jamsil(700,3em);
    @include variables.fz(15em,12em,8em,4em);
    margin-bottom: 50px;
  }

  .page404-content{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 3vw;
    p{
      @include variables.jamsil(400,3em);
      @include variables.fz(3em,2em,1.5em,1.3em);
    }
  }
  .page404-link--index{
    color: #cbcbcb;
    &:hover {
      color: black;
    }
  }
}

